<template>
	<view>
		
		<view @click="tiaozhuan_jiaofei()" class="hengfang" style="width: 100vw;text-align: center;background: #FFFFFF;height: 20vw;">
			<view style="width: 33vw;margin-top: 5vw;">
				<view v-if="list_1.fee" style="color: #95d6d6;">￥ {{list_1.fee}}</view>
				<view v-if="!list_1.fee" style="color: #95d6d6;">￥ 0</view>
				<view style="font-size: 80%;margin-top: 1vw;color: #dadada;">待缴费</view>
			</view>
			<view style="width: 0.5vw;background: #F1F1F1;height: 15vw;margin-top: 3vw;"></view>
			<view style="width: 33vw;;margin-top: 5vw;">
				<view v-if="list_1.closeDate" style="color: #95d6d6;">{{list_1.closeDate}}</view>
				<view v-if="!list_1.closeDate" style="color: #95d6d6;">无</view>
				<view style="font-size: 80%;margin-top: 1vw;color: #dadada;">截至缴费</view>
			</view>			
			<view style="width: 0.5vw;background: #F1F1F1;height: 15vw;margin-top: 3vw;"></view>
			<view style="width: 33vw;;margin-top: 5vw;">
				<view v-if="list_1.lastPayTime" style="color: #95d6d6;">{{list_1.lastPayTime}}</view>
				<view v-if="!list_1.lastPayTime" style="color: #95d6d6;">无</view>
				<view style="font-size: 80%;margin-top: 1vw;color: #dadada;">近期缴费</view>
			</view>
		</view>
		
		<view v-if="list_2.length<1" style="text-align: center;width: 100vw;margin-top: 40vw;">
			<image src="/static/img/gg_01.png" style="width: 30vw;height: 30vw;"></image>
			<view style="margin-top: 2vw;">没有记录</view>
			</view>

		<view v-for="(item,index) in list_2" :key="index" style="margin-top: 1vw;width: 100vw;background: #FFFFFF;">
			<view @click="kaiguan_xuanze(index)" style="margin-top: 2vw;width: 100vw;height: 15vw;line-height: 15vw;" class="hengfang">
				<view style="width: 9vw;font-size: 150%;padding-left: 3vw;">{{item.usePeriod}}</view>
				<view style="width: 59vw;font-size: 75%;line-height: 15vw;padding-left: 1vw;color: #b5b5b5;">{{item.startDate}}~{{item.endDate}}</view>
				<view style="width: 22vw;">￥{{item.receivedFee}}</view>
				<view style="width: 10vw;font-size: 120%;">
					<text v-if="item.isDefault == 0" class="cuIcon-unfold"></text>
					<text v-if="item.isDefault != 0" class="cuIcon-fold"></text>
					</view>
			</view>
			<view v-if="item.isDefault != 0" v-for="(item1,index2) in item.sonLogs" :key="index2">
				<view  style="width: 100vw;height: 3rpx;background: #f1f1f1;"></view>
				<view  style="width: 100vw;">
					<view style="width: 100vw;height: 15vw;" class="hengfang">
						<view style="width: 15vw;text-align: center;">
							<view class="dingwei_juedui" style="width: 1vw;height: 7.5vw;background: #f1f1f1;margin-left: 7vw;z-index: 0;margin-top: 0vw;"></view>
							<image class="dingwei_juedui" src="/static/ico/lb_01.png" style="width: 8vw;height: 8vw;margin-top: 4.5vw;margin-left: -4vw;"></image>
						</view>
						<view style="width: 85vw;">
							<view style="width: 85vw;" class="hengfang">
								<view style="width: 65vw;">
									<view style="margin-top: 2vw;">{{item1.payitemName}}</view>
									<view style="color: #cacaca;">缴费时间 {{item1.entryTime}}</view>
								</view>
								<view style="width: 20vw;line-height:15vw;">
									<view style="color: #737373;font-size: 120%;">- {{item1.sonReceivedFee}}</view>
								</view>
							</view>
							<view style="width: 85vw;height: 0.5vw;background: #f1f1f1;"></view>
						</view>
					</view>
				</view>
			</view>
			
		</view>



	</view>
</template>

<script>
	export default {
		data() {
			return {
				list_1:{},
				list_2:[],
				
			}
		},
		methods: {
			tiaozhuan_jiaofei:function(){
				uni.navigateTo({
					url:"../jiaofei_list/jiaofei_list"
				})
			},
			kaiguan_xuanze:function(res){
				let _this = this
				_this.list_2[res].isDefault = !_this.list_2[res].isDefault
			},
			huoqu_list_1:function(){
				let _this = this
				let data = {
				}
				_this.wangluo_qingqiu('/wx/ipms/getfeeChargelisttitle', data, true, true, 'GET').then(value => {
					if (value) {
						_this.list_1 = value
					}
				})
			},
			huoqu_list_2:function(){
				let _this = this
				let data = {
				}
				_this.wangluo_qingqiu('/wx/ipms/getfeechargelogshistory', data, true, true, 'GET').then(value => {
					if (value) {
						_this.list_2 = value
					}
				})
			}
		},
		onShow:function(){
			let _this = this
			_this.huoqu_list_1()
			_this.huoqu_list_2()
		}
	}
</script>

